import React from 'react'
import Layout from "../components/layout"
import backIcon from '../images/close.png'
import AniLink from "gatsby-plugin-transition-link/AniLink"
import Gallery from '../components/Gallery.js'
import { graphql } from 'gatsby'

export default class GalleryPage extends React.Component {
    render() {
        return <Layout>
            <div style={{ 
                display: 'flex', 
                alignItems: 'center', 
                marginTop: 24, 
                marginBottom: 24 
            }}>
                    <AniLink 
                        style={{
                            display: 'flex',
                            alignItems: 'center',
                            justifyContent: 'center',
                        }}
                        to="/" 
                        replace 
                        cover 
                        bg="#f5587b" 
                        direction="right"
                    >
                        <img style={{ margin: 0 }} src={ backIcon } alt="" />
                    </AniLink>
                    <h2 style={{ marginLeft: 24 }}>Gallery</h2>
                </div>
                <Gallery images={ this.props.data.gallery.edges }/>
            </Layout>
    }
}

export const query = graphql`
    fragment GalleryFragment on File {
        id
        relativePath
        childImageSharp {
            fluid {
                ...GatsbyImageSharpFluid
            }
            sizes {
                presentationHeight
                presentationWidth
            }
        }
    }

    query {
        gallery: allFile( filter: {
            sourceInstanceName: {eq: "gallery"},
            extension: {in: ["png", "jpg"]}
        }) {
            edges {
                node {
                    ...GalleryFragment
                }
            }
        }
    }
`
